<template>
  <v-card flat>
    <table>
      <tbody>
        <tr>
          <td>
            <v-text-field
              v-model="postCode"
              label="Postal code"
              outlined
            />
          </td>
          <td>
            <v-text-field
              v-model="state"
              label="State"
              outlined
            />
          </td>
          <td>
            <v-text-field
              v-model="city"
              label="City"
              outlined
            />
          </td>
          <td>
            <v-text-field
              v-model="street"
              label="Street name"
              outlined
            />
          </td>
          <td>
            <v-text-field
              v-model="number"
              label="Street number"
              outlined
            />
          </td>
          <td>
            <v-text-field
              v-model="buildingName"
              label="Building Name"
              outlined
            />
          </td>
          <td>
            <v-select
              :items="$buildingTypes"
              label="Status"
              v-model="status"
              outlined
            />
          </td>
          <td>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="#09b" text @click="saveCallback"> Save </v-btn>
    </v-card-actions> -->
  </v-card>
</template>

<script>

export default {
  name: 'BuildingListItem',

  props: ['editedItem', 'save', 'buildingId'],

  data: () => ({
    selectedBuilding: null
  }),

  computed: {
    postCode: {
      get () {
        return this.editedItem.postCode
      },
      set (val) {
        this.updateAddressComponents('postCode', val)
      }
    },
    state: {
      get () {
        return this.editedItem.state
      },
      set (val) {
        this.updateAddressComponents('state', val)
      }
    },
    city: {
      get () {
        return this.editedItem.city
      },
      set (val) {
        this.updateAddressComponents('city', val)
      }
    },
    street: {
      get () {
        return this.editedItem.street
      },
      set (val) {
        this.updateAddressComponents('street', val)
      }
    },
    number: {
      get () {
        return this.editedItem.number
      },
      set (val) {
        this.updateAddressComponents('number', val)
      }
    },
    buildingName: {
      get () {
        return this.editedItem.buildingName
      },
      set (val) {
        this.updateBuildingData('buildingName', val)
      }
    },
    status: {
      get () {
        return this.editedItem.status
      },
      set (val) {
        this.updateBuildingData('status', val)
      }
    }
  },
  methods: {
    updateAddressComponents (propName, propValue) {
      const building = localStorage.getItemByName('selectedBuilding')
      building.addressComponents[propName] = propValue
      localStorage.setItemByName('selectedBuilding', building)
      this.$emit('update:editedItem', Object.assign(this.editedItem, { [propName]: propValue }))
    },
    updateBuildingData (propName, propValue) {
      const building = localStorage.getItemByName('selectedBuilding')
      building[propName] = propValue
      localStorage.setItemByName('selectedBuilding', building)
      this.$emit('update:editedItem', Object.assign(this.editedItem, { [propName]: propValue }))
    },
    saveCallback () {
      this.$emit('update:save', true)
    }
  },
  watch: {
    'editedItem._id': {
      immediate: true,
      handler (val) {
        this.selectedBuilding = val
        this.$emit('update:buildingId', val)
      }
    }
  },
  mounted () {
    this._getBuildingDetails(this.editedItem._id)
      .then((response) => {
        this.selectedBuildingData = response
        localStorage.setItemByName('selectedBuilding', this.selectedBuildingData)
        this.$emit('update:buildingId', this.selectedBuildingData._id)
      })
  }
}
</script>
